import { html } from 'lit';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import { PlaceholderSvg16 } from '../../../../docs/helpers/placeholder-svg';

export type TMarketTileTemplateArgs = Partial<HTMLMarketTileElement> & {
  // demo
  backgroundColor?: string;
  backgroundImage?: boolean;
  color?: string;
  hint?: string;
  indicator?: string;
  label?: string;
  leadingAccessory?: boolean;
  subtext?: string;
};

export const MarketTileTemplate = ({
  // demo
  backgroundColor,
  backgroundImage,
  color,
  hint,
  indicator,
  label,
  leadingAccessory,
  subtext,
  // props
  disabled,
  showActions,
  size,
  interactive,
  selected,
}: TMarketTileTemplateArgs) => html`
  <market-tile
    style="background-color: ${backgroundColor}; color: ${color};"
    ?aria-disabled=${disabled}
    ?show-actions=${showActions}
    ?interactive=${interactive}
    ?selected=${selected}
    size="${ifDefined(size)}"
  >
    ${leadingAccessory
      ? html`<market-accessory slot="leading-accessory"> ${PlaceholderSvg16('currentcolor')} </market-accessory>`
      : null}
    ${indicator ? html`<span slot="indicator">${indicator}</span>` : null}
    ${hint ? html`<span slot="hint">${hint}</span>` : null} ${label ? html`<span slot="label">${label}</span>` : null}
    ${subtext ? html`<span slot="subtext">${subtext}</span>` : null}
    ${backgroundImage
      ? html`
          <img
            slot="media"
            src="data:image/svg+xml;base64,"
          />
        `
      : null}
  </market-tile>
`;
